<template>
    <div class="common-layout">
      <el-container>
        <el-header class="header_color">教务系统学生管理</el-header>
        <el-container>
          <el-row class="tac">
          <!--以下是侧边栏-->
            <el-menu
              active-text-color="#04111C"
