<script setup>
  import { ref } from 'vue'
  const desc = ref('七年级1班学生统计') 
  const stuShow = ref([])
  const students = [
    {grade: 7, class:1, name: '张三', age: 18, sex: '男', date: '2022-01-01'},
    {grade: 7, class:2, name: '王五', age: 20, sex: '男', date: '2022-01-03'},
    {grade: 7, class:1, name: '赵六', age: 17, sex: '女', date: '2022-01-04'},
    {grade: 8, class:1, name: '小吴', age: 16, sex: '男', date: '2022-01-05'},
    {grade: 8, class:1, name: '小李', age: 15, sex: '男', date: '2022-01-06'},
    {grade: 8, class:2, name: '小刘', age: 19, sex: '女', date: '2022-01-07'},
    {grade: 9, class:1, name: '小张', age: 17, sex: '女', date: '2022-01-09'},
    {grade: 9, class:2, name: '大伟', age: 17, sex: '男', date: '2022-01-09'},
  ]
  const selectFunc = (index) =>{
    console.log(index)
    let stuGrade = parseInt(index.charAt(0)) + 6 //转换为数值型
    let stuClass = parseInt(index.slice(2))
    let stuFilter = students.filter((stu) => stu.grade === stuGrade && stu.class === stuClass)
    console.log(stuFilter)
    stuShow.value=stuFilter//更新响应式数据
    desc.value = stuGrade + `年级${stuClass}班学生统计`//更新描述
  }
  //选中复选框
  let idArr = []
  const selected = (data)=>{
    console.log("selected:", data)
    idArr=[]//重置
    data.forEach((value) => (idArr.push(value.id)))
    console.log(idArr)
  }

</script>

